<template>
<!--    对首页功能卡做封装，支持下一步画布流动布局，动态插入功能-->
    <el-card shadow="always" class="SelectionCard" v-on:click.native="MSCardClick()">
        <el-row>
            <el-col :span="6">
                <slot name="selection-card-img"></slot>
            </el-col>
            <el-col :span="18">
                <el-row><h3><slot class="selection-card-title" name="selection-card-title"></slot></h3></el-row>
                <el-row><slot class="selection-card-text" name="selection-card-text"></slot></el-row>
            </el-col>
        </el-row>
    </el-card>
</template>

<script>
    export default {
        name: "ModelSelectionCard",
        props: {
            path: String,
        },
        methods: {
            MSCardClick() {
                this.$router.push(this.path);
            },
        }
    }
</script>

<style scoped>
    .SelectionCard {
        margin: 25px;
        background: #535458;
        border: 0;
        box-shadow: 0 5px 10px rgba(0, 0, 0, .12), 0 0 15px rgba(0, 0, 0, .04);
        cursor: pointer;
        min-height: 128px;
    }
    .SelectionCard:hover {
        background: grey;
    }
    .el-col {
        color: white;
    }
</style>
